









import Vue from "vue";
import * as tf from "@tensorflow/tfjs";
export default Vue.extend({
  data() {
    return {
      model: <tf.LayersModel | null>null,
      info: "",
    };
  },
  async mounted(){
    this.info = "";
    let modelurl = location.origin + <string>require('../assets/models/gan1/model.json');
    let model = await tf.loadLayersModel(modelurl);
    this.model = model;
    model.summary(undefined, undefined, msg => {
       this.info = this.info + msg + "\n";
    });
  }
});
