import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/tensorflow/minst1',
    component: () => import('../views/Minst1.vue')
  },
  {
    path: '/tensorflow/minst2',
    component: () => import('../views/Minst2.vue')
  },
  ,
  {
    path: '/tensorflow/gan1',
    component: () => import('../views/Gan1.vue')
  },
  {
    path: '/stencil',
    component:()=> import('../views/Stencil.vue')
  },
  {
    path: '/tune',
    component: ()=> import('../views/Tune.vue')
  },
  {
    path: '/gerberview',
    component: ()=> import('../views/GerberView.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
